import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NFormComponent } from './n-form.component';
import { NFormTemplateComponent } from './n-form-template/n-form-template.component';
import { NFormReactiveComponent } from './n-form-reactive/n-form-reactive.component';

const routes: Routes = [
  {
    path: '',
    component: NFormComponent,
    children: [
      {
        path: 'template',
        component: NFormTemplateComponent
      },
      {
        path: 'reactive',
        component: NFormReactiveComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NFormRoutingModule { }
