import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { matchPass } from 'src/app/core/validatetion/validate';

@Component({
  selector: 'app-n-form-reactive',
  templateUrl: './n-form-reactive.component.html',
  styleUrls: ['./n-form-reactive.component.scss']
})
export class NFormReactiveComponent implements OnInit {
  formReactive: FormGroup;

  constructor(
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.formReactive = this.fb.group({
      firstName: ['', Validators.required],
      lastName: [''],
      email: [''],
      password: [''],
      confirmPassword: [''],
      address: this.fb.group({
        houseNumber: [''],
        street: [''],
        city: ['']
      }),
      skills: this.fb.array([
        this.fb.control(''),
        this.fb.control('')
      ])
    }, {
      validator: matchPass('password', 'confirmPassword')
    });
  }

  action() {
    console.log(this.formReactive);
  }

  get skills() {
    return this.formReactive.controls.skills as FormArray;
  }

  addMoreSkill() {
    this.skills.push(this.fb.control(''));
  }

}
